.app__navbar {
  width: 100%;

  display: flex;
  justify-content: space-between;
  align-items: center;

  padding: 1rem 2rem;
  background: rgba(255, 255, 255, 0.25);
  backdrop-filter: blur(4px);
  -webkit-backdrop-filter: blur(4px);

  border: 1px solid rgba(255, 255, 255, 0.18);
  position: fixed;
  z-index: 2;
}
.logonew {
  ///  font-size: large;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  span {
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-weight: 600;
    color: rgb(25, 116, 116);
  }
}
.app__navbar-logo {
  display: flex;
  justify-content: flex-start;
  align-items: center;

  img {
    width: 90px;
    height: 20px;

    @media screen and (min-width: 2000px) {
      width: 180px;
      height: 40px;
    }
  }
}

.app__navbar-links {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;

  list-style: none;

  li {
    margin: 0 1rem;
    cursor: pointer;

    flex-direction: column;

    div {
      width: 5px;
      height: 5px;
      border-radius: 50%;
      background: transparent;

      margin-bottom: 5px;
    }
    a {
      color: var(--gray-color);
      text-decoration: none;

      flex-direction: column;
      text-transform: uppercase;

      font-weight: 500;

      transition: all 0.3s ease-in-out;

      &:hover {
        // '&' this will address to the parent classs -> which will be anchor tag
        color: (var(--secondary-color));
      }
    }
    &:hover {
      div {
        background: var(--secondary-color);
      }
    }
  }
  @media screen and (max-width: 900px) {
    display: none;
  }
}
.app__navbar-button {
  width: 100px;
  height: 50px;
  border-radius: 28px;
  position: absolute;

  right: 30px;
  cursor: pointer;
  box-shadow: 0 0 25px rgba($color: #000000, $alpha: 0.2);
  border: 2px solid rgba(255, 255, 255, 0.5);
  background: var(--primary-color);

  color: var(--gray-color);
  font-size: 1rem;
  text-transform: uppercase;
  font-weight: 500;

  transition: all 0.3s ease-in-out;
  &:hover {
    transform: scale(1, 1);
    color: var(--secondary-color);
  }
}
.app__navbar-button-mob{
  width: 90px;
  height: 50px;
  border-radius: 25px;
  position: absolute;
  left: 25px;
  //right: -25px;

  cursor: pointer;
  box-shadow: 0 0 25px rgba($color: #000000, $alpha: 0.2);
  border: 2px solid rgba(255, 255, 255, 0.5);
  background: var(--primary-color);

  color: var(--gray-color);
  font-size: 1rem;
  text-transform: uppercase;
  font-weight: 500;

  &:hover {
    transform: scale(1, 1);
    color: var(--secondary-color);
  }
}
.app__navbar-menu {
  width: 35px;
  height: 35px;
  border-radius: 50%;
  position: relative;

  display: flex;
  justify-content: center;
  align-items: center;
  background: var(--secondary-color);

  svg {
    width: 70%;
    height: 70%;
    color: var(--white-color);
  }
  div {
    position: fixed;
    top: 0;
    bottom: 0;
    right: 0;
    z-index: 5;

    padding: 1rem;

    width: 80%;
    height: 100vh;

    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;

    background: url("../../assets/bgWhite.png");
    background-color: var(--white-color);
    background-size: cover;
    background-repeat: repeat;

    box-shadow: 0 0 20px rgba(94, 86, 86, 0.15);

    svg {
      width: 35px;
      height: 35px;
      color: var(--secondary-color);
      margin: 0.5rem 1rem;
    }
    ul {
      list-style: none;
      margin: 0;
      padding: 0;
      height: 100%;
      width: 100%;

      display: flex;
      justify-content: flex-start;
      align-items: flex-start;
      flex-direction: column;
      li {
        margin: 1rem;
        a {
          color: var(--gray-color);
          text-decoration: none;
          font-size: 1rem;
          text-transform: uppercase;
          font-weight: 500;

          transition: all 0.3s ease-in-out;
          &:hover {
            color: var(--secondary-color);
          }
        }
      }
    }
    @media screen and (min-width: 900px) {
      display: none;
    }
  }
  @media screen and (min-width: 900px) {
    display: none;
  }
}
